.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  height: 50px;
  background-color: #fff;
  box-shadow: 0 -1px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2px;
  z-index: 1000;
  box-sizing: border-box;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
